/* ----------- iPhone 5 and 5S ----------- */

/* Portrait and Landscape */
@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 568px)
  and (-webkit-min-device-pixel-ratio: 2) {
       .body {
        width: 100%;
       }
       .div {
        width: 100%;
       }
       .button {
        width: 100%;
       }
       .h1 {
        width: 100%;
       }
       .p {
        width: 100%;
       }
       .input {
        width: 100%;
       }
       .html {
        width: 100%;
       }

}

/* Portrait */
@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 568px)
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: portrait) {
     .body {
      width: 100%;
     }
     .div {
      width: 100%;
     }
     .button {
      width: 100%;
     }
     .h1 {
      width: 100%;
     }
     .p {
      width: 100%;
     }
     .input {
       width: 100%;
     }
     .html {
      width: 100%;
     }

}

/* Landscape */
@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 568px)
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: landscape) {
      .body {
      width: 100%;
     }
     .div {
      width: 100%;
     }
     .button {
      width: 100%;
     }
     .h1 {
      width: 100%;
     }
     .p {
      width: 100%;
     }
     .input {
        width: 100%;
     }
     .html {
      width: 100%;
     }
}



